import React from 'react';import { 
    BrowserRouter, Switch, Route, Redirect 
    }
from 'react-router-dom';

import App from './App';
import Navigation from './Components/Navbar';

function Main() {
    return (
        <div>
            <BrowserRouter>
                <Navigation/>
                <Switch>
                    <Route exact path="/" component={App}></Route>
                </Switch>
            </BrowserRouter>
        </div>
    );
}

export default Main;